    import {mapState,mapGetters,mapMutations} from 'vuex'
	export default {
		data() {
			return {
				title: '新页面',
				data:""
			}
		},
        computed:{
            ...mapState(['colorIndex','colorList']),
            ...mapGetters(['currentColor'])
        },
		onLoad(e){
			if(e.data){
				this.data = e.data;
			}
            uni.$on('postMsg',(res)=>{
                uni.showModal({
                    content: `收到uni.$emit消息:${res.msg}`,
                    showCancel: false
                })
            })
		},
        onUnload() {
            uni.$off('postMsg')
        },
        methods:{
            ...mapMutations(['setColorIndex']),
            navToNvue(){
                uni.navigateTo({
                    url:'new-nvue-page-1'
                })
            },
            navToVue(){
                uni.navigateTo({
                    url:'new-vue-page-2'
                })
            }
        }
	}
